import axios from '@axios'

export default {
  namespaced: true,
  state: {
    companies: [],
  },
  getters: {},
  mutations: {
    SET_RECORD(state, companies) {
      state.companies = companies
    },
    UPDATE_RECORD(state, data) {
      const catIndex = state.companies.findIndex(cat => cat.id === Number(data.id))
      Object.assign(state.companies[catIndex], data)
    },
    REMOVE_RECORD(state, itemId) {
      const caseIndex = state.companies.findIndex(d => d.id === itemId)
      state.companies.splice(caseIndex, 1)
    },
  },
  actions: {
    store(ctx, data) {
      return new Promise((resolve, reject) => {
        axios.post('admin/companies', data)
          .then(res => {
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    fetch({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get('admin/companies')
          .then(response => {
            commit('SET_RECORD', response.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    fetchName({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get('admin/companies/name')
          .then(response => {
            commit('SET_RECORD', response.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    update({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post('admin/companies/update', data)
          .then(res => {
            // console.log(Object.fromEntries(data.entries()))
            commit('UPDATE_RECORD', Object.fromEntries(data.entries()))
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    // eslint-disable-next-line camelcase
    delete({ commit }, userId) {
      return new Promise((resolve, reject) => {
        axios.delete(`admin/companies/${userId}`)
          .then(res => {
            commit('REMOVE_RECORD', userId)
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
  },
}
