import axios from '@axios'

export default {
  namespaced: true,
  state: {
    employees: [],
  },
  getters: {},
  mutations: {
    SET_RECORD(state, categories) {
      state.employees = categories
    },
    UPDATE_RECORD(state, data) {
      const catIndex = state.employees.findIndex(cat => cat.id === data.id)
      Object.assign(state.employees[catIndex], data)
    },
    REMOVE_RECORD(state, itemId) {
      const caseIndex = state.employees.findIndex(d => d.id === itemId)
      state.employees.splice(caseIndex, 1)
    },
  },
  actions: {
    store(ctx, data) {
      return new Promise((resolve, reject) => {
        axios.post('admin/employees', data)
          .then(res => {
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    fetch({ commit }) {
      return new Promise((resolve, reject) => {
        axios.get('admin/employees')
          .then(response => {
            commit('SET_RECORD', response.data.data.data)
            resolve(response)
          })
          .catch(error => reject(error))
      })
    },
    edit({ commit }, data) {
      return new Promise((resolve, reject) => {
        axios.post('admin/employees/update', data)
          .then(res => {
            commit('UPDATE_RECORD', data)
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
    // eslint-disable-next-line camelcase
    delete({ commit }, id) {
      return new Promise((resolve, reject) => {
        axios.delete(`admin/employees/${id}`)
          .then(res => {
            commit('REMOVE_RECORD', id)
            resolve(res)
          })
          .catch(error => reject(error))
      })
    },
  },
}
